/* MAIN STYLES                  */
/* ---------------------------- */

:root {
  --max-content-width: 940px;
}

body {
  position: relative;
  font-family: "Lato";
  color: #000;
  min-height: 100%;
  width: 100%;
  background-color: #ffffff;

  background: {
    repeat: repeat-y;
    position: center center;
    size: 1000px 100vh;
  }

  // Inset border
  box-shadow: inset 0 0 0 9px #000;

  // Inner rounded corners
  &::before {
    border: 9px solid #000;
    display: block;
    content: "";
    border-radius: 22px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
  a {
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

.header {
  padding: 2rem 3rem 0;
  grid-column: 1/-1;
  position: relative;
  border-bottom: 3px solid #000;

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 9px;
    left: 9px;
    right: 9px;
    bottom: 0;
    border-radius: 13px 13px 0 0;
  }

  .header-content {
    max-width: var(--max-content-width);
    margin: 0 auto;
    display: flex;
    align-items: baseline;
    gap: 1rem;
  }
}

.site-main {
  font-size: 16px;
  line-height: 24px;

  padding: {
    top: 3rem;
    right: 3rem;
  }
}

.home {
  .site-main {
    display: flex;
    flex-flow: column;
    position: relative;
    z-index: 1;
  }
}

p {
  font-family: "Lato";
  margin-bottom: 1.2rem;

  &:not(:last-child) {
    margin-bottom: 0.7rem;
  }
}

.site-title-group {
  display: flex;
  align-items: baseline;
  gap: 1rem;
  z-index: 1;
}

.site-title {
  font-family: "Oswald";
  font-weight: 500;
  font-size: 1.7rem;
  text-transform: uppercase;
  grid-column: 1/-1;
  z-index: 9999;
  position: relative;
  max-width: 960px;
  margin-bottom: 0;

  a {
    text-decoration: none;
  }
}

.site-description {
  margin: 0;
}

.main-navigation {
  display: none;
}

.site-content {
  max-width: var(--max-content-width);
  margin: 0 auto;
  display: grid;
  grid-template-columns: 3fr 1fr;
}

