.widget-area {
  padding-top: 3rem;
}

.entry-header {
  h1 {
    font-size: 1.4rem;
    margin-bottom: 1rem;
    border-top: 1px solid #000;
    padding-top: 0.8rem;
  }
}

.entry-feature-img {
  position: relative;
  justify-content: flex-end;
  padding-bottom: 0.5rem;
  background-size: cover;
  background-position: center center;
  border-radius: 8px;
  width: 100%;
  border: 2px solid;
  min-height: 10.4rem;
  margin: {
    bottom: 0.3rem;
    right: 1rem;
  }
}

.entry-content {
  max-width: 650px;

  > a[rel="wp-video-lightbox"]:last-of-type {
    margin-bottom: 1rem;
    display: block;
  }

  .yotu-playlist {
    padding: 0;
  }
  .yotu-videos {
    padding-top: 0;
  }
  pre {
    margin: 0 1rem 0 0;
    float: left;

    &:last-child {
      margin-right: 0;
    }
  }
}

ul.block-list {
  width: auto;

  li {
    width: auto;
    margin-bottom: 0;

    a[rel="wp-video-lightbox"] {
      margin-bottom: 0;
    }
  }
}

.wp-block-group {
  margin-bottom: 1rem;
  gap: 1rem;

  &.img-group_1-1 {
    display: grid;
    grid-template-columns: 1fr 1fr;

    img {
      width: auto !important;
    }
  }
  &.img-group_2-1 {
    display: grid;
    grid-template-columns: 2fr 1fr;

    img {
      width: auto !important;
    }
  }
  &.img-group_1-2 {
    display: grid;
    grid-template-columns: 1fr 2fr;

    img {
      width: auto !important;
    }
  }
  &.img-group_1-1-1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

    img {
      width: auto !important;
    }
  }
  &.w-equalize {
    img {
      aspect-ratio: 1 / 1;
    }
  }

  &.img-group_P-L {
    display: grid;
    grid-template-columns: 1fr 1.76fr;
  }

  &.img-group_P-Pv {
    display: grid;
    grid-template-columns: 1.329fr 1fr;
  }

  // .wp-block-image:last-child {
  //   margin-bottom: 1rem;
  // }

  &.is-layout-flex {
    align-items: flex-start;

    &.is-horizontal {
      gap: 1rem;

      &.wp-block-group-is-layout-flex {
        width: auto;
        gap: 1rem;

        .yotu-playlist,
        .yotu-mode-list {
          padding-top: 0;
          width: min-content;
          height: 180px;

          // ul {
          //   width: auto;

          //   li {
          //     width: auto;
          //     margin-bottom: 0;

          //     a[rel="wp-video-lightbox"] {
          //       margin-bottom: 0;
          //     }
          //   }
          // }

          .yotu-video-thumb-wrp {
            width: 185px;
            margin-right: 0;
          }
        }
        .yotu-playlist {
          width: 200px;
          overflow: hidden;
          overflow-y: auto;
          scrollbar-width: thin;
          border: 2px solid #000;
          border-radius: 0.5rem;
          flex-shrink: 0;
        }
        .yotu-video {
          width: auto;

          .yotu-video-title {
            font-size: 0.7rem;
            padding: {
              left: 0.3rem;
              top: 0.3rem !important;
            }
            line-height: 1.9 !important;
            font-weight: bold;
          }
        }
      }
    }
  }

  .align-left {
    margin-bottom: 0;

    + .wpvl_auto_thumb_box_wrapper {
      float: left;
      margin-right: 1rem;
    }
    .yotu-playlist {
      margin-right: 1rem;
    }
  }
  &.align-left {
    float: left;
  }
}

