article {
  padding-bottom: 1rem;
}

.wp-block-video {
  border: 2px solid #000;
  border-radius: 0.5rem;
  overflow: hidden;
  margin-bottom: 1rem;
}

.byline {
  display: none;
}

.video_lightbox_auto_anchor_image {
  width: 200px;
  height: 150px;
  border: 2px solid #000;
  overflow: hidden;
  border-radius: 0.5rem;
}

.wp-block-list {
  float: left;
  padding-left: 1rem;
  margin-top: 0;
  position: relative;
  top: -5px;
}

.widget-title {
  text-transform: uppercase;
  font-weight: 100;
}

.widget {
  ul {
    margin: 0;
    padding-left: 1rem;
  }
}

code {
  font-family: "Lato", sans-serif;
}

.wp-block-heading {
  font-size: 1.2rem;
  margin-bottom: 0.7rem;
}

.wp-block-image {
  margin-bottom: 1rem;

  img {
    border: 2px solid;
    border-radius: 0.5rem;
  }
  .alignleft:last-of-type {
    margin-right: 1rem;
    float: left; /// conflict on svan?
  }
  figure.alignleft {
    margin: {
      top: 0;
      right: 1rem;
    }
  }
  figure.alignright {
    margin: {
      top: 0;
      left: 1rem;
    }
    margin-top: 0;
  }
  &.w-60 {
    // width: 60%;
    .alignleft,
    .alignright {
      width: 60% !important;

      img {
        width: auto !important;
      }
    }
  }
  &.w-40 {
    // width: 40%;
    .alignleft,
    .alignright {
      width: 40% !important;

      img {
        width: auto !important;
      }
    }
  }
}

.align-left {
  margin-bottom: 0;

  + .wpvl_auto_thumb_box_wrapper {
    float: left;
    margin-right: 1rem;
    margin-bottom: 1rem;
  }
}
